                goodsInfo.seriesName == null || goodsInfo.seriesName == ""
                  ? "-"
                  : goodsInfo.seriesName
              }}</span>
            </el-tooltip>
          </div>
        </div> -->
        <div class="book-intro2">
          <span>中图法分类：</span>
          <span class="ellipsis-twoRow">
            {{ goodsInfo.classification ? goodsInfo.classification : "-" }}
          </span>
        </div>
        <div class="book-intro2">
          <span>自定义分类：</span>
          <span class="ellipsis-twoRow">
