          <div>
            <span>主管单位：</span>
            <el-tooltip
              :content="goodsInfo.subTopicName"
              placement="top"
              v-ak-tooltip-auto-show
            >
              <span class="ellipsis-aRow">{{
                goodsInfo.hostUnit ? goodsInfo.hostUnit : "-"
              }}</span>
            </el-tooltip>
          </div>
          <div>
            <span>主办单位：</span>
            <el-tooltip
              :content="goodsInfo.seriesName"
              placement="top"
              v-ak-tooltip-auto-show
            >
              <span class="ellipsis-aRow">{{
                goodsInfo.sponsor ? goodsInfo.sponsor : "-"
              }}</span>
            </el-tooltip>
          </div>
        </div>
        <div class="book-intro2">
          <span>中图法分类：</span>
          <span class="ellipsis-twoRow">
            {{ goodsInfo.classification ? goodsInfo.classification : "-" }}
          </span>
        </div>
        <div class="book-intro2">
          <span>自定义分类：</span>
          <span class="ellipsis-twoRow">
            {{ goodsInfo.selfName ? goodsInfo.selfName : "-" }}
          </span>
        </div>
