    </div>
    <div
      class="resource-file"
      v-if="goodsInfo.goodsType != 'paper' && goodsInfo.status != 3"
    >
      <!-- <div class="title theme-title">资源文件</div>
      <div
        class="resource-item"
        v-for="(item, index) in sourceList"
        :key="index"
      >
        <div class="resource-name ellipsis-aRow" @click="toRead(index)">
          {{ item.sourceName ? item.sourceName : item.fileName }}
        </div>
        <div :class="item.sourceStatus == '0' ? 'active' : 'type'">
          {{ item.sourceStatus == 0 ? "付费" : "免费" }}
        </div>
      </div> -->
    </div>
    <div class="goods-detail">
      <div class="title theme-title">商品详情</div>
      <div
        class="detail ql-editor ql-snow"
        v-html="goodsInfo.introduction"
      ></div>
      <noData
        v-if="goodsInfo.introduction == '' || goodsInfo.introduction == null"
      ></noData>
    </div>
  </div>
</template>
<script>
import "quill/dist/quill.core.css"; // 引入样式
import "quill/dist/quill.snow.css"; // snow theme
import "quill/dist/quill.bubble.css"; // bubble theme
import noData from "@/components/noData.vue";
import { OuchnOss } from "@/plugins/OuchnOss";
import {
  TeachingClassList, //下拉树
  teachingAttrList //字典值
} from "@/api/bookstore/bookstore";
export default {
  data() {
    return {
      acquireSourcelist: [], //下拉书
      acquireSourcelist2: [], //开本
      acquireSourcelist3: [], //版次
      ouchnOss2: null
    };
  },
  props: {
    goodsInfo: Object,
    sourceList: Array,
    subjectId: String,
    goodsId: String,
    pthis: Object
  },
  components: {
    noData,
    audioread: () => import("@/views/onlineReading/components/audioread.vue")
  },
  created() {
    this.getresource_source();
    this.ouchnoss2 = new OuchnOss(true);
  },
  methods: {
    getresource_source() {
      //中图法分类
      TeachingClassList("law_type").then(res => {
        this.acquireSourcelist = res.data;
      });
      //开本
      teachingAttrList("oc_goods_format").then(res => {
        this.acquireSourcelist2 = res.data;
      });
      //版次
      teachingAttrList("oc_goods_edition").then(res => {
        this.acquireSourcelist3 = res.data;
      });
    },
    toRead(index) {
      // 如果该商品不是已下架就可以去阅读
      if (this.goodsInfo.status != 5) {
        //判断是从专题进入还是书城进入
        if (this.subjectId) {
          this.$router.push({
            path: "/onlineReading",
            query: {
              goodsId: this.goodsId,
              resourceType: "subject",
              resource: "bookCity",
              relevanceId: this.subjectId,
              readdefault: index
            }
          });
        } else {
          this.$router.push({
            path: "/onlineReading",
            query: {
              goodsId: this.goodsId,
              resourceType: "goods",
              resource: "bookCity",
              relevanceId: this.goodsId,
              readdefault: index
            }
          });
        }
      }
    }
  }
};
</script>

<style scoped lang="scss">
.content-intro {
  box-sizing: border-box;
  padding: 15px 40px;
  .detail-info {
    .book-info-intro {
      font-size: 14px;
      font-family: PingFang SC;
      font-weight: 400;
      color: #666666;
      line-height: 26px;
      .book-intro1 {
        width: 880px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 14px;
        > div {
          display: flex;
          align-items: center;
          span:last-child {
            width: 130px;
          }
        }
      }
      .book-intro1:nth-of-type(2) {
        width: 860px;
        justify-content: flex-start;
        > div {
          > span:last-child {
            width: 230px;
          }
        }
        > div:last-child {
          margin-left: 66px;
          > span:last-child {
            width: 250px;
          }
        }
      }
      .book-intro2 {
        width: 950px;
        display: flex;
        margin-bottom: 14px;
        > span:first-child {
          max-width: 100px;
          display: block;
        }
        > span:last-child {
          max-width: 850px;
        }
      }
    }
  }
  .resource-file {
    margin-bottom: 20px;
    .resource-item {
      text-decoration: none;
      box-sizing: border-box;
      padding-right: 20px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 32px;
      background: #f2f2f2;
      font-size: 14px;
      font-family: PingFang SC;
      font-weight: 400;
      color: #666666;
      margin-bottom: 10px;
      .resource-name {
        max-width: 705px;
        box-sizing: border-box;
        padding-left: 6px;
        cursor: pointer;
      }
      .type {
        color: #2e9adb;
      }
      .active {
        color: #ff5353;
      }
      .type-zip {
        width: 48px;
        height: 20px;
        line-height: 20px;
        text-align: center;
        background: #f2f2f2;
        border: 1px solid #2e9adb;
        border-radius: 6px;
        font-size: 14px;
        font-family: PingFang SC;
        font-weight: 400;
        color: #2e9adb;
        cursor: pointer;
      }
    }
  }
  .goods-detail {
    .detail {
      font-size: 14px;
      font-family: PingFang SC;
      font-weight: 400;
      color: #666666;
      line-height: 26px;
      >>> img {
        max-width: 100%;
      }
    }
  }
  .title {
    font-size: 16px;
    margin-bottom: 25px;
  }
}
</style>
